-alt,\n);\n\n@function Color($key) {\n  $color: map-get($colors,$key);\n  @return $color;\n}","article {\n  >:first-child {\n    margin-top: 0;\n  }\n  >:last-child {\n    margin-bottom: 0;\n  }\n}","@import \"~@/styles/theme/breaks\";\n\niframe {\n  &:only-child {\n    width: 100%;\n    max-width: 100%;\n    height: auto;\n    min-height: 16em;\n    // margin: 0 -1rem;\n    @include Break( max-width Breaks(2) ){\n      min-width: 100vw;\n      // padding: 0 1rem;\n    }\n  }\n}","//////////////////////////////\n// Default Variables\n//////////////////////////////\n$Breakpoint-Settings: (\n  'default media': all,\n  'default feature': min-width,\n  'default pair': width,\n\n  'force all media type': false,\n  'to ems': false,\n  'transform resolutions': true,\n\n  'no queries': false,\n  'no query fallbacks': false,\n\n  'base font size': 16px,\n\n  'legacy syntax': false\n);\n\n$breakpoint: () !default;\n\n//////////////////////////////\n// Imports\n//////////////////////////////\n@import \"breakpoint/settings\";\n@import 'breakpoint/context';\n@import 'breakpoint/helpers';\n@import 'breakpoint/parsers';\n@import 'breakpoint/no-query';\n\n@import 'breakpoint/respond-to';\n\n@import \"breakpoint/legacy-settings\";\n\n//////////////////////////////\n// Breakpoint Mixin\n//////////////////////////////\n\n@mixin breakpoint($query, $no-query: false) {\n  @include legacy-settings-warning;\n\n  // Reset contexts\n  @include private-breakpoint-reset-contexts();\n\n  $breakpoint: breakpoint($query, false);\n\n  $query-string: map-get($breakpoint, 'query');\n  $query-fallback: map-get($breakpoint, 'fallback');\n\n  $private-breakpoint-context-holder: map-get($breakpoint, 'context holder') !global;\n  $private-breakpoint-query-count: map-get($breakpoint, 'query count') !global;\n\n  // Allow for an as-needed override or usage of no query fallback.\n  @if $no-query != false {\n    $query-fallback: $no-query;\n  }\n\n  @if $query-fallback != false {\n    $context-setter: private-breakpoint-set-context('no-query', $query-fallback);\n  }\n\n  // Print Out Query String\n  @if not breakpoint-get('no queries') {\n    @media #{$query-string} {\n      @content;\n    }\n  }\n\n  @if breakpoint-get('no query fallbacks') != false or breakpoint-get('no queries') == true {\n\n    $type: type-of(breakpoint-get('no query fallbacks'));\n    $print: false;\n\n    @if ($type == 'bool') {\n      $print: true;\n    }\n    @else if ($type == 'string') {\n      @if $query-fallback == breakpoint-get('no query fallbacks') {\n        $print: true;\n      }\n    }\n    @else if ($type == 'list') {\n      @each $wrapper in breakpoint-get('no query fallbacks') {\n        @if $query-fallback == $wrapper {\n          $print: true;\n        }\n      }\n    }\n\n    // Write Fallback\n    @if ($query-fallback != false) and ($print == true) {\n      $type-fallback: type-of($query-fallback);\n\n      @if ($type-fallback != 'bool') {\n        #{$query-fallback} & {\n          @content;\n        }\n      }\n      @else {\n        @content;\n      }\n    }\n  }\n\n  @include private-breakpoint-reset-contexts();\n}\n\n\n@mixin mq($query, $no-query: false) {\n  @include breakpoint($query, $no-query) {\n    @content;\n  }\n}\n","@import \"~@/styles/theme/scale\";\n@import \"~@/styles/theme/fonts\";\n\n@import \"./elements/all\";\n@import \"./elements/table\";\n@import \"./elements/article\";\n@import \"./elements/iframe\";\n\n:root {\n  font-size: $root;\n  @media (max-width:68rem){\n    font-size: calc(12pt + .33vmin);\n  }\n}\n\nhtml, body {\n  margin: 0;\n  padding: 0;\n}\nhtml {\n  font-family: \"futura-pt\", Futura, sans-serif;\n}\nbody {\n  font-size: 1em;\n  line-height: 2;\n}\n\nh1, h2, h3, h4, h5, h6{\n  line-height: 1.2;\n  margin: unset;\n}\nimg {\n  display: block;\n  margin: 0 auto;\n  max-width: 100%;\n  height: auto;\n}\n\np, ul, ol, dl, figure, blockquote {\n  margin: 1rem 0;\n}\n\npre, code {\n  font-family: $ff-mono;\n}","@function Size( $n:false ){\n  @if( $n ){\n    @return nth($sizes,$n);\n  }\n  @return nth($sizes,1);\n}\n\n$root: 14pt !default;\n$sizes:\n  1em    // 16pt\n  1.25em // 20pt\n  1.75em // 28pt\n  2.25em // 36pt\n  3em    // 48pt\n  4.5em  // 72pt\n  ;//SIZES","\n$ff-sans:  \"futura-pt\", Futura, Verdana, sans-serif;\n\n$ff-serif: \"Butler\", Georgia, serif;\n\n$ff-alt:   nth($ff-serif,2), serif;\n\n$ff-mono:  Source Code Pro, monospace;","@import \"~@/styles/base/external\";\n@import \"~@/styles/base/doc\";\n\na { color: inherit }","@import \"~@/styles/theme/colors\";\n\n$themes: cream light dark rust !default;\n\n@mixin UI_THEMES_EXTEND( $themes: $themes ){\n  @each $theme in $themes {\n    &_#{$theme} {\n      @extend %#{$theme} !optional;\n      &_translucent {\n        @extend %#{$theme}, %#{$theme}_translucent !optional;\n      }\n    }\n  }\n}\n\n%cream {\n  @extend .UiTheme;\n  color: Color(slate);\n  background: Color(cream);\n}\n%cream_translucent {\n  color: Color(slate);\n  background: rgba(Color(cream),.99);\n}\n\n%light {\n  @extend .UiTheme;\n  color: Color(slate);\n  background: Color(light);\n}\n%light_translucent {\n  color: Color(slate);\n  background: rgba(Color(light),.99);\n}\n\n%dark {\n  @extend .UiTheme;\n  color: Color(silver);\n  background: Color(dark);\n}\n%dark_translucent {\n  color: Color(silver);\n  background: rgba(Color(dark),.99);\n}\n\n%rust {\n  @extend .UiTheme;\n  color: Color(matte);\n  background: Color(alt);\n}\n%rust_translucent {\n  color: Color(matte);\n  background: rgba(Color(alt),.99);\n}\n\n.UiTheme {\n  & {\n    transition: 0s ease-out;\n  }\n  & { @include UI_THEMES_EXTEND; }\n}"],"sourceRoot":""}]);

